<template>
  <section class="grid">
    <div>
      <h1>Rest (axios / jwt)</h1>
      <div class="actions">
        <button @click="GET">GET</button>
        <button @click="POST">POST</button>
        <button @click="PUT">PUT</button>
        <button @click="DELETE">DELETE</button>
      </div>
      <div class="form">
        <input type="text" v-model="base" placeholder="base">
        <input type="text" v-model="endpoint" placeholder="endpoint">
        <input type="text" v-model="token" placeholder="jwt token" class="token">
        <textarea rows="10" v-model="body" placeholder="key: value"></textarea>
        <pre>{{jsonBody}}</pre>
      </div>
    </div>
    <div class="result">
      <h3>Status</h3>
      <pre v-if="status" v-html="status"></pre>
      <h3>Data</h3>
      <pre v-if="data" v-html="data"></pre>
      <h3>Headers</h3>
      <pre v-if="headers" v-html="headers"></pre>
      <h3>Request</h3>
      <pre v-if="request" v-html="request"></pre>
      <h3>Config</h3>
      <pre v-if="config" v-html="config"></pre>
    </div>
  </section>
</template>

<script>
import axios from "axios";

function formatJSON(json) {
  json = JSON.stringify(json, null, 1);
  json = json.replace(/["][-\w]+["]\:/g, "<span>$&</span>");
  return json;
}

export default {
  data() {
    return {
      base: "",
      endpoint: "",
      body: "",
      token: "",
      data: null,
      headers: null,
      request: null,
      status: null,
      config: null
    };
  },
  computed: {
    url() {
      return this.base + this.endpoint;
    },
    jsonBody() {
      // const json = ;
      if (this.body) {
        return JSON.parse(this.body);
      }
    }
  },
  methods: {
    resetData() {
      this.data = null;
      this.headers = null;
      this.request = null;
      this.status = null;
      this.config = null;
    },
    formatResponse(response) {
      console.log(response);
      this.data = formatJSON(response.data);
      this.headers = formatJSON(response.headers);
      this.status = formatJSON(response.status);
      this.config = formatJSON(response.config);
      return response;
    },
    formatError(error) {
      console.log(error);
      this.data = formatJSON(error);
      this.status = error.response.status;
      return error;
    },
    GET() {
      this.resetData();
      this.jwt();
      axios
        .get(this.url)
        .then(response => this.formatResponse(response))
        .catch(error => this.formatError(error));
    },
    POST() {
      this.resetData();
      this.jwt();
      axios
        .post(this.url, this.jsonBody)
        .then(response => this.formatResponse(response))
        .catch(error => this.formatError(error));
    },
    PUT() {
      this.resetData();
      this.jwt();
      axios
        .put(this.url, this.jsonBody)
        .then(response => this.formatResponse(response))
        .catch(error => this.formatError(error));
    },
    DELETE() {
      this.resetData();
      this.jwt();
      axios
        .delete(this.url, this.jsonBody)
        .then(response => this.formatResponse(response))
        .catch(error => this.formatError(error));
    },
    jwt() {
      if (this.token) {
        axios.defaults.headers.common["Authorization"] = "Bearer " + this.token;
      }
    }
  },
  watch: {
    base() {
      window.localStorage.base = this.base;
    },
    endpoint() {
      window.localStorage.endpoint = this.endpoint;
    },
    body() {
      window.localStorage.body = this.body;
    },
    token() {
      window.localStorage.token = this.token;
    }
  },
  created() {
    this.base =
      window.localStorage.base !== "undefined" ? window.localStorage.base : "";
    this.endpoint =
      window.localStorage.endpoint !== "undefined"
        ? window.localStorage.endpoint
        : "";
    this.body =
      window.localStorage.body !== "undefined" ? window.localStorage.body : "";
    this.token =
      window.localStorage.token !== "undefined"
        ? window.localStorage.token
        : "";
  }
};
</script>

