  color: white;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}

pre {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
  font-family: "IBM Plex Mono", monospace;
}
