  color: greenyellow;
}

.form {
  display: grid;
  grid-template-columns: minmax(150px, 1fr) minmax(150px, 1fr);
  grid-gap: 10px;
}

input,
textarea {
  font-size: 1.2rem;
  font-family: "Avenir", Arial, Helvetica, sans-serif;
  border: none;
  padding: 10px;
  border-radius: 2px;
}

textarea {
  font-size: 0.9rem;
  font-family: "IBM Plex Mono", monospace;
